<script>
export default {
	data(){
		return {
			udata:this.$store.state.user
		}
	},
	computed:{
		img(){
			return this.udata.himg || "https://ss0.bdstatic.com/94oJfD_bAAcT8t7mm9GUKT-xh_/timg?image&quality=100&size=b4000_4000&sec=1523692796&di=6f2584abae193c60d25fcc4895274d5f&src=http://img.zcool.cn/community/01786557e4a6fa0000018c1bf080ca.png"
		}
	}
}
</script>

